(function() {
  angular.module('interreps').controller('RegisterController', function($rootScope, $scope, $timeout, $interval, $localStorage, FirebaseService, rep, competitions, prices) {
    'ngInject';
    $scope.rep = rep;
    $scope.allCompetitions = competitions;
    $scope.prices = prices;
    $scope.totalCost = $scope.rep.totalCost || 0;
    $scope.selectedPrice = {};
    $scope.img = "../assets/images/" + $scope.rep.user + ".jpg";
    $scope.selectedCompetitions = $scope.rep.competitions || [];
    $scope.user = $localStorage.user;
    $scope.participant = {};
    $scope.participants = $scope.rep.participants || [];
    $scope.team = {
      partitipants: [],
      competition: {}
    };
    $scope.teams = [];
    $scope.futebolPrices = ['Futebol', 'Futebol + 1', 'Futebol + 2', 'Futebol + 3', 'Futebol + Queimada + 3 modalidades'];
    $scope.queimadaPrices = ['Queimada', 'Queimada 1', 'Queimada 2', 'Queimada 3', 'Futebol + Queimada + 3 modalidades'];
    $scope.methods = {
      addParticipant: function() {
        if ($scope.participant !== {}) {
          $scope.participants.push($scope.participant);
          return $scope.participant = {};
        }
      },
      removeParticipant: function(index) {
        return $scope.participants.splice(index, 1);
      },
      clear: function() {
        return $scope.participants = [];
      },
      save: function() {
        console.log($scope.participants);
        FirebaseService.updateRepParticipants($scope.rep.id, $scope.participants);
        FirebaseService.updateRepCompetitions($scope.rep.id, $scope.selectedCompetitions);
        return FirebaseService.updateRepTotalCost($scope.rep.id, $scope.totalCost);
      }
    };
    $scope.$watchCollection('selectedCompetitions', function(newVal, oldVal) {
      $scope.totalCost = 0;
      return _.each(newVal, function(item) {
        var price;
        price = item.team ? item.price * item.team.length : item.price * 0;
        return $scope.totalCost += price;
      });
    });
  });

}).call(this);



