(function() {
  angular.module("interreps").service("FirebaseService", function($http, $q) {
    var auth, database;
    database = firebase.database();
    auth = firebase.auth();
    return {
      getCurrentAuthuser: function() {
        return auth.currentUser;
      },
      getAllUsers: function() {
        return database.ref('users').once('value').then(function(result) {
          return result.val();
        });
      },
      getUserById: function(id) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('users').child(index).once('value').then(function(result) {
          return result.val();
        });
      },
      getAllReps: function() {
        return database.ref('reps').once('value').then(function(result) {
          return result.val();
        });
      },
      getRepById: function(id) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child(index).once('value').then(function(result) {
          return result.val();
        });
      },
      getRepCompetitions: function(id) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child('index').child('competitions').once('value').then(function(result) {
          return result.val();
        });
      },
      updateRepCompetitions: function(id, competitions) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child(index).child('competitions').set(competitions);
      },
      updateRepParticipants: function(id, participants) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child(index).child('participants').set(participants);
      },
      updateRepTotalCost: function(id, totalCost) {
        var index;
        id = parseInt(id);
        index = id - 1;
        return database.ref('reps').child(index).child('totalCost').set(totalCost);
      },
      createRep: function(rep) {
        return database.ref('reps').once('value').then(function(result) {
          var ref, repsLength;
          repsLength = result.val().length;
          ref = database.ref('reps');
          return ref.child('/' + repsLength).set(rep);
        });
      },
      getAllCompetitions: function() {
        return database.ref('competitions').once('value').then(function(result) {
          return result.val();
        });
      },
      getCompetitionsArray: function() {
        return database.ref('allCompetitions').once('value').then(function(result) {
          return result.val();
        });
      },
      updateLeaderboard: function(key, leaderboard) {
        var reps;
        reps = [];
        database.ref('reps').once('value').then(function(result) {
          return reps = result.val();
        });
        return database.ref('allCompetitions').once('value').then(function(result) {
          var index;
          index = _.findIndex(result.val(), function(item) {
            return item.name === key;
          });
          return database.ref('allCompetitions').child(index).child('leaderboard').set(leaderboard).then(function(result) {
            return _.each(leaderboard, function(register) {
              var compIndex, i, rep;
              i = _.findIndex(reps, function(rep) {
                return rep.name === register.rep;
              });
              rep = _.find(reps, function(rep) {
                return rep.name === register.rep;
              });
              compIndex = _.findIndex(rep.competitions, function(comp) {
                return comp.name === key;
              });
              return database.ref('reps').child(i).child('competitions').child(compIndex).child('position').set(register.position);
            });
          });
        });
      },
      getRules: function() {
        return database.ref('rules').once('value').then(function(result) {
          return result.val();
        });
      },
      updateRules: function(rules) {
        return database.ref('rules').set(rules);
      },
      getPrices: function() {
        return database.ref('prices').once('value').then(function(result) {
          return result.val();
        });
      }
    };
  });

}).call(this);



